}

// BODY
#invisible_h1{
  color: transparent;
}

.welcome_block{
  color: white;
  height: 820px;
  background-image: url(../../img/front.jpg);
  background-size: cover;
  background-attachment: fixed;
  text-align: center;
}

.overlay{
  background-color: rgba(0,0,0,.5);
  height: 820px;
  width: 100vw;
}

#welcome_block_text{
  font-size: 3vw;
  text-shadow: 2px 2px #000;
  margin-top: 180px;
  line-height: 180%;
}

#main_services_heading{
  text-align: center;
  margin-top: 100px;
  font-size: 50px;
}

#main_services_text_block{
  margin-left: auto;
  margin-right: auto;
  font-size: 20px;
  border: 0px;
}

